\n  data: {\n    logoUrl:\n      \"http://img1.vued.vanthink.cn/vued08aa73a9ab65dcbd360ec54659ada97c.png\",\n    target: \"Weex\"\n  },\n  methods: {\n    update() {\n      console.log(\"update\");\n      this.target = \"Nono\";\n      console.log(\"target:\", this.target);\n\n      modal.toast({\n        message: \"update weex 2 nono\",\n        duration: 1\n      });\n    },\n    jump(event) {\n      console.log(\"will jump\");\n      navigator.push(\n        {\n          //http://192.168.0.68:8081/dist/hello.js\n          url: \"http://dotwe.org/raw/dist/6fe11640e8d25f2f98176e9643c08687.bundle.js\", //web用 http://192.168.0.68:8081/web/index.html?page=/dist/hello.js\n          animated: \"false\"\n        },\n        event => {\n          modal.toast({ message: \"callback: \" + event });\n        }\n      );\n    },\n    sendMsg() {\n      console.log(\"sendMsg\");\n      myMoudle.sendMessage(\"weex发送指令给native\")\n      myMoudle.openPageByUrl(\"http://dotwe.org/raw/dist/6fe11640e8d25f2f98176e9643c08687.bundle.js\")\n      \n      modal.toast({\n        message: \"sendMsg 2 native\",\n        duration: 1\n      });\n    },\n  }\n};\n</script>\n<style>\n.wrapper {\n  align-items: center;\n  height: 1000px;\n  background-color: #888;\n  padding-top: 50px;\n}\n.title {\n  padding-top: 20px;\n  padding-bottom: 20px;\n  font-size: 35px;\n  color: antiquewhite;\n}\n.logo {\n  width: 360px;\n  height: 156px;\n  margin-top: 50px;\n}\n.desc {\n  padding-top: 20px;\n  color: azure;\n  font-size: 24px;\n}\n\n.btn {\n  width: 350px;\n  background-color: antiquewhite;\n  margin-top: 10px;\n  margin-bottom: 10px;\n  align-items: center;\n  text-align: center;\n  padding: 10px;\n}\n</style>\n"],"sourceRoot":""}]);

